

import Dexie from 'dexie';

var state = {
  db: new Dexie('PlantumlEditor'),
  scheme: '++id,text,src,created',
  version: 1,
  data: []
};

var mutations = {
  changeDbName: function changeDbName() {
    Dexie.getDatabaseNames(function (databases) {
      if (databases.indexOf(null) !== -1 && databases.indexOf('PlantumlEditor') === -1) {
        var oldDb = new Dexie();
        oldDb.version(state.version).stores({
          plantuml: state.scheme
        });
        oldDb.plantuml.toCollection().toArray().then(function (data) {
          state.db.plantuml.bulkAdd(data).then(function () {
            Dexie.delete('undefined');

            window.localStorage.setItem('Dexie.DatabaseNames', '["PlantumlEditor"]');
            location.reload();
          });
        });
      }
    });
  },
  defineScheme: function defineScheme(state) {
    state.db.version(state.version).stores({
      plantuml: state.scheme
    });
  },
  getHistories: function getHistories(state) {
    state.db.plantuml.reverse().toArray().then(function (data) {
      state.data = data;
    });
  },
  save: function save(state, plantumlEditor) {
    state.db.plantuml.add({
      text: plantumlEditor.text,
      src: plantumlEditor.src,
      created: new Date().toLocaleString()
    });
  },
  delete: function _delete(state, id) {
    state.db.plantuml.delete(id);
  }
};

var actions = {
  defineScheme: function defineScheme(context) {
    context.commit('changeDbName');
    context.commit('defineScheme');
  },
  getHistories: function getHistories(context) {
    context.commit('getHistories');
  },
  save: function save(context, plantumlEditor) {
    context.commit('save', plantumlEditor);
    context.commit('getHistories');
  },
  delete: function _delete(context, id) {
    context.commit('delete', id);
    context.commit('getHistories');
  }
};

export default {
  state: state,
  mutations: mutations,
  actions: actions
};


